.hamburger {
  position: fixed;
  top: 4.8rem;
  right: 6.45rem;
  z-index: 9999;
}
@media only screen and (max-width: 43.75em) {
  .hamburger {
    top: 4.8rem;
    right: 4rem;
  }
}
@media only screen and (max-width: 31.25em) {
  .hamburger {
    top: 4.8rem;
    right: 3rem;
  }
}
@media only screen and (max-width: 20em) {
  .hamburger {
    top: 4.8rem;
    right: 2.5rem;
  }
}
.hamburger__toggler {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  cursor: pointer;
  width: 5rem;
  height: 5rem;
  opacity: 0;
}
.hamburger__toggler:checked + .hamburger__box > div {
  transform: rotate(135deg);
}
.hamburger__toggler:checked + .hamburger__box > div::before, .hamburger__toggler:checked + .hamburger__box > div::after {
  top: 0;
  transform: rotate(90deg);
}
.hamburger__toggler:checked:hover + .hamburger__box > div {
  transform: rotate(225deg);
}
.hamburger__toggler:checked ~ .hamburger__menu {
  visibility: visible;
}
.hamburger__toggler:checked ~ .hamburger__menu > div {
  transform: scale(1);
  transition-duration: 0.75s;
}
.hamburger__box {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  width: 4.5rem;
  height: 4.5rem;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
  border-radius: 50rem;
}
.hamburger__box > div {
  position: relative;
  width: 100%;
  height: 0.2rem;
  background-color: #ff2e27;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.4s ease;
}
.hamburger__box > div::before, .hamburger__box > div::after {
  content: "";
  position: absolute;
  z-index: 1;
  top: -1rem;
  width: 100%;
  height: 0.2rem;
  background: inherit;
}
.hamburger__box > div::after {
  top: 1rem;
}
.hamburger__menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hamburger__overlay {
  background-color: rgba(255, 46, 39, 0.9);
  border-radius: 50%;
  width: 300vh;
  height: 300vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: none;
  transform: scale(0);
  transition: all 0.4s ease;
}
.hamburger__overlay > div {
  text-align: center;
  max-width: 90vh;
  max-height: 100vh;
  opacity: 1;
  transition: opacity 0.4s ease;
}
.hamburger__item {
  list-style-type: none;
}
.hamburger__link {
  color: #ffffff;
  text-decoration: none;
  font-family: "Montserrat", Arial;
  font-size: 1.8rem;
  text-transform: uppercase;
  padding: 1rem;
  display: inline-block;
  transition: color 0.4s ease;
}

